"use client";
import React, { useState } from "react";

const leftnav = () => {
  const navData = ["Find a Mentor", "Pending Requests", "Scheduled Meet", "All Request", "Ai Help" , "Posts" , "Trending News" , "Create Course" ];
  return (
    <>
      <nav className="text-black  h-screen ">
        <div className="text-center my-10 font-extrabold text-4xl text-blue-700">
          SkillShare
        </div>
        <ul className="text-center my-10 space-y-2">
          {/* <li>My Courses</li>
                <li>Lessons</li>
                <li>Messages</li>
                <li>Rewards</li>
                <li>Settings</li> */}
          {navData.map((items, index) => (
            <li
              key={index}
              className= "font-medium text-lg mx-4 hover:bg-blue-200 hover:text-blue-500 py-2 rounded-lg cursor-pointer"
            >
              {items}
            </li>
          ))}
        </ul>
      </nav>
    </>
  );
};

export default leftnav;
